<template>
  <div class="index-right">
    <div class="index_order">
      <el-row :gutter="20">
        <el-col :span="6">
          <div
            class="grid-content bg-purple"
            style="background-image: linear-gradient(230deg, #759bff, #843cf6); border-radius: 10px;"
          >
            <img src="../assets/images/index_11.png" class="right" alt />
            <div class="left">
              <p>总订单</p>
              <p>102,400</p>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div
            class="grid-content bg-purple"
            style="background-image: linear-gradient(230deg, #fc5286, #fbaaa2); border-radius: 10px;"
          >
            <img src="../assets/images/index_22.png" class="right" alt />
            <div class="left">
              <p>总销售额</p>
              <p>102,400</p>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div
            class="grid-content bg-purple"
            style="background-image: linear-gradient(230deg, #ffc480, #ff763b); border-radius: 10px;"
          >
            <img src="../assets/images/index_33.png" class="right" alt />
            <div class="left">
              <p>今日订单数</p>
              <p>102,400</p>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div
            class="grid-content bg-purple"
            style="background-image: linear-gradient(230deg, #0e4cfd, #6a8eff); border-radius: 10px;"
          >
            <img src="../assets/images/index_44.png" class="right" alt />
            <div class="left">
              <p>今日销售额</p>
              <p>102,400</p>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="index_echarst">
      <div id="main" style="width:100%; height:470px;"></div>
    </div>
  </div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      ctime: [],
      num: [],
      num2: []
    };
  },
  async mounted() {
    let data = await this.$axios.get(
      "http://127.0.0.1:3333/counter.php?a=goods&field=date"
    );
    // console.log(data);
    data.data.data.forEach(item => {
      // console.log(item.ctime)
      this.ctime.push(item.ctime);
      this.num.push(Number(item.num));
    });
    let data2 = await this.$axios.get(
      "http://127.0.0.1:3333/counter.php?a=users&field=date"
    );
    // console.log(data2);
    data2.data.data.forEach(item => {
      this.num2.push(Number(item.num));
    });
    var myChart = echarts.init(document.getElementById("main"));
    myChart.setOption({
      tooltip: {
        trigger: "axis",
        axisPointer: {
          // 坐标轴指示器，坐标轴触发有效
          type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
        }
      },
      legend: {
        data: ["商品数量", "用户数量"]
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true
      },
      xAxis: [
        {
          type: "category",
          data: this.ctime
        }
      ],
      yAxis: [
        {
          type: "value"
        }
      ],
      series: [
        {
          name: "商品数量",
          type: "bar",
          data: this.num
        },
        {
          name: "用户数量",
          type: "bar",

          data: this.num2
        }
      ]
    });
  }
};
</script>

<style>
.el-row {
  margin-bottom: 20px;
}
.el-row:last-child {
  margin-bottom: 0;
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #ffffff;
}
.bg-purple {
  background: #ffffff;
}
.bg-purple-light {
