}
.grid-content {
  /* border-radius: 4px; */
  min-height: 138px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.index-right .right {
  float: right;
  margin: 60px 30px 0 0;
  width: 50px;
}
.index-right .left {
  float: left;
  margin: 36px 0 0 28px;
}
.index-right .left > p:first-child {
  line-height: 30px;
  color: #ffffff;
  font-size: 24px;
  margin-bottom: 10px;
  letter-spacing: 1px;
}
.index-right .left > p:last-child {
